<template>
  <div class="house-fight">
    <!-- pre -->
    <div v-if="state==='pre'" class="fight-pre-box">
      <div class="fight-pre-bg" :style="{ backgroundImage: 'url(' + fireBg + ')' }"></div>
      <div class="fight-pre-ability">關卡怪力值<br><span style="font-size:24px;">5</span></div>
      <img class="fight-pre-word" src="@/assets/images/ling/fight-word.png" alt="fight-word">
      <div class="fight-pre-text">怪奇會施展怪力干擾<br>讓怪力求保持在中間區域即可戰勝</div>
      <div class="fight-pre-btn-box">
        <img @click="backToCompass" class="fight-pre-btn" src="@/assets/images/ling/run-away.png" alt="run-away">
        <img @click="goToFight" class="fight-pre-btn" src="@/assets/images/ling/enter-in.png" alt="enter-in">
      </div>
    </div>
    <!-- ing -->
    <div v-else-if="state==='ing'" class="fight-ing-box">
      <div class="fight-ing-bg" :style="{ backgroundImage: 'url(' + fireBg + ')' }"></div>
      <img class="fight-ing-ling-icon" src="@/assets/images/lulu/lulu-shine-half-icon.png" alt="fight-word">
      <div class="fight-ing-time">{{ time }}</div>
      <div class="fight-ing-bar" :style="{ backgroundImage: 'url(' + ballBar + ')' }">
        <img
          :style="{ marginLeft:  ballPosition + 'px' }"
          class="fight-ing-ball"
          src="@/assets/images/ling/play-ball.png"
          alt="play-ball">
      </div>
      <div class="fight-ing-btn-box">
        <img @click="goLeft" class="fight-ing-btn" src="@/assets/images/ling/go-left.png" alt="go-left">
        <img @click="goRight" class="fight-ing-btn" src="@/assets/images/ling/go-right.png" alt="go-right">
      </div>
    </div>
    <!-- lose -->
    <div @click="goToPre" v-else-if="state==='lose'" class="fight-lose-box">
      <div class="fight-lose-bg" :style="{ backgroundImage: 'url(' + fireBg + ')' }"></div>
      <img class="fight-lose-ling-icon" src="@/assets/images/lulu/lulu-shine-icon.png" alt="fight-word">
      <div class="fight-lose-ch">殘念</div>
       <div class="fight-lose-text">請重新補充能力值再戰</div>
    </div>
    <!-- winPre -->
    <div v-else-if="state==='winPre'" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/lulu/lulu-grey-icon.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">露露</div>
        <div class="house-0-frame">
          <div>不！不！不⋯⋯我、我輸了⋯⋯我要被帶回去那個地方了嗎？看來只要是弱者，就註定被利用、註定被奴役呢。可恨啊，你們就繼續這樣濫用你們的能力破壞這個世界吧，遲早會出現你們對付不了的怪奇，消滅你們的⋯⋯</div>
          <div @click="winGame" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- win -->
    <div @click="goToCollect" v-else-if="state==='win'" class="fight-lose-box">
      <div class="fight-lose-bg" :style="{ backgroundImage: 'url(' + fireBg + ')' }"></div>
        <img class="fight-lose-ling-icon" src="@/assets/images/lulu/lulu-grey-icon.png" alt="fight-word">
        <div class="fight-lose-ch">捕獲怪奇</div>
        <div class="house-0-all-btn">此線完結</div>
      </div>
  </div>
</template>

<script>

import fireBg from '@/assets/images/lulu/lulu-fight-bg.png'
import greenOpacityBg from '@/assets/images/ling/fight-bd-opacity.png'
import ballBar from '@/assets/images/ling/vertical-bar.png'
export default {
  name: 'LuluHouseFight',
  components: {

  },
  data () {
    return {
      fireBg: fireBg,
      greenOpacityBg: greenOpacityBg,
      ballBar: ballBar,
      state: 'pre',
      time: 15,
      ballPosition: 0
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
  },
  methods: {
    backToCompass () {
      this.$store.commit('changeHeader', true)
      this.$router.push({
        path: './monster-compass'
      })
    },
    goToFight () {
      if (this.power < 5) {
        alert('能力值不足哦')
      } else {
        this.state = 'ing'
        this.$store.commit('changePower', 5)
        // 塞入 local storage
        let vuesList = JSON.parse(localStorage.getItem('vuexList'))
        vuesList.power = this.power
        localStorage.setItem('vuexList', JSON.stringify(vuesList))
        //
        this.gameStart()
      }
    },
    gameStart () {
      // 時間開始
      let round1 = setInterval(() => {
        this.time--
      }, 1000)

      setTimeout(() => {
        if (this.ballPosition < -160) {
          this.ballPosition = -250
        } else {
          this.ballPosition = this.ballPosition - 90
        }
      }, 2000)

      setTimeout(() => {
        if (this.ballPosition > 100) {
          this.ballPosition = 250
        } else {
          this.ballPosition = this.ballPosition + 150
        }
      }, 5000)

      setTimeout(() => {
        if (this.ballPosition > 60) {
          this.ballPosition = 250
        } else {
          this.ballPosition = this.ballPosition + 190
        }
      }, 9000)

      setTimeout(() => {
        if (this.ballPosition < -150) {
          this.ballPosition = -250
        } else {
          this.ballPosition = this.ballPosition - 100
        }
      }, 11000)

      setTimeout(() => {
        if (this.ballPosition < -150) {
          this.ballPosition = -250
        } else {
          this.ballPosition = this.ballPosition - 100
        }
      }, 13000)

      // 時間結束
      setTimeout(() => {
        clearInterval(round1)
        if (this.ballPosition < 150 && this.ballPosition > -150) {
          this.winPre()
        } else {
          alert('你輸了！')
          this.state = 'lose'
          this.time = '15'
          this.ballPosition = '0'
        }
      }, 15000)
    },
    winPre () {
      this.state = 'winPre'
      // 裡頭選項在點 winGame
    },
    winGame () {
      alert('你贏了！')
      this.$store.commit('changeHeader', true)
      // this.$store.commit('changeNoteDot', true)
      // this.$store.commit('changeNotDotHeadTwo', true)
      // this.$store.commit('changeNotDotHead', true)
      this.$store.commit('changeFrameDot', true)
      this.$store.commit('luluWin', true)
      this.state = 'win'
      this.$store.commit('getLuluCard', 'grey')
      // 塞入 local storage
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      vuesList.isLuluWin = true
      vuesList.luluCardType = 'grey'
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
      //
    },
    goLeft () {
      this.ballPosition = this.ballPosition - 10
    },
    goRight () {
      this.ballPosition = this.ballPosition + 10
    },
    goToPre () {
      this.state = 'pre'
    },
    goToCollect () {
      this.$router.push({
        path: './monster-collect'
      })
    }
  },
  computed: {
    power () {
      return this.$store.state.power
    }
  }
}
</script>
<style lang="scss" scoped>

  .house-fight {
    min-height: 100vh;
    background: linear-gradient(#000 70%, #95ff00);
    touch-action: manipulation;

    .fight-pre {

      &-box {
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-ability {
        position: absolute;
        top: 15px;
        right: 20px;
        padding: 4px;
        color: black;
        background-color: #95ff00;
        border-radius: 4px;
        text-align: center;
        z-index: 1;
      }

      &-word {
        width: 100px;
        height: 100%;
        z-index: 1;
      }

      &-text {
        color: #95ff00;
        font-size: 16px;
        margin-top: 16px;
        text-align: center;
        z-index: 1;
      }

      &-btn-box {
        margin-top: 16px;
        display: flex;
        justify-content: space-around;
        z-index: 1;
      }

      &-btn {
        width: 80px;
        height: 80px;
        margin: 0px 20px;
      }
    }

    .fight-ing {

      &-box {
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-ling-icon {
        position: absolute;
        top: 40px;
        width: 70vw;
        height: auto;
      }

      &-time {
        position: absolute;
        top: 20px;
        left: 20px;
        text-align: center;
        font-size: 30px;
        color: #95ff00;
        z-index: 2;
      }

      &-bar {
        margin-top: 160px;
        display: flex;
        justify-content: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        width: 300px;
        height: 50px;
        border-radius: 40px;
        z-index: 2;
      }

      &-ball {
        width: 50px;
        height: 50px;
      }

      &-btn-box {
        margin-top: 16px;
        display: flex;
        justify-content: space-around;
        width: 300px;
        z-index: 1;
      }

      &-btn {
        width: 80px;
        height: 80px;
      }

    }

    .fight-lose {

      &-box {
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        z-index: 0;
      }

      &-ling-icon {
        width: 140px;
        height: auto;
        z-index: 2;
      }

      &-ch {
        text-align: center;
        font-size: 40px;
        color: #95ff00;
        z-index: 2;
      }

      &-text {
        color: #95ff00;
        text-align: center;
        z-index: 2;
      }
    }
  }

  .house-0 {

    &-box {
      position: relative;
      width: 100%;
      min-height: 100vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-repeat: no-repeat;
      background-size: contain;
      background-position-x: center;
      background-position-y: top;
      background-image: url('~@/assets/images/lulu/lulu-fight-bg.png');
    }

    &-ghost {
      width: 60%;
      height: auto;
      position: absolute;
      top: 0px;
    }

    &-talk-box {
      display: flex;
      flex-direction: column;
    }

    &-head {
      padding: 4px 16px;
      margin: 150px auto 0px;
      text-align: center;
      background: #95ff00;
      color: black;
      border-radius: 30px;
      opacity: 0.7;
      z-index: 1;
    }

    &-frame {
      width: 100vw;
      margin-top: 10px;
      max-height: 200px;
      padding: 20px;
      color: white;
      overflow: scroll;
      color: black;
      background: #95ff00;
      z-index: 1;
      opacity: 0.7;
    }

    &-triangle-box {
      margin-top: 16px;
    }

    &-triangle {
      margin: auto;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 15px 15px 0 15px;
      border-color: black transparent transparent transparent;
    }
  }

  .house-0-all-btn {
    width: 100%;
    color: white;
    text-align: center;
    background: gray;
    margin-top: 30px;
    padding: 20px 10px;
  }

</style>



